body {
  background: #f8f8f8;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Open Sans', sans-serif; }

#camera {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%); }

#snapshot {
  display: none; }

#debug {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);
  padding: 8px;
  color: red;
  z-index: 9999999;
  font-size: 12px; }

#snapshotLimitOverlay {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 70vmin;
  height: 70vmin;
  transform: translateX(-50%) translateY(-50%);
  text-align: center; }

#snapshotLimitOverlay:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 0 999px rgba(0, 0, 0, 0.4); }

#flipCamera {
  display: none;
  position: fixed;
  right: 16px;
  top: 16px;
  border: none;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  font-size: 1px;
  z-index: 99999999;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  background-size: cover;
  background-image: url("data:image/svg+xml;base64,"); }

.errorScreen {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  color: #D32F2F;
  font-family: monospace; }
